.sales {
  max-width: 313.0434782609px;
  margin: 0 auto;
}
.sales-head {
  margin-bottom: 23.4782608696px;
  font-size: 28.6956521739px;
  font-weight: 300;
  letter-spacing: 0.01em;
  color: #94b33a;
  text-align: center;
}
.sales-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 13.0434782609px;
}
.sales-item + .sales-item {
  margin-top: 21.7391304348px;
  padding-top: 21.7391304348px;
  border-top: 1px solid #494949;
}
.sales-pic {
  flex: 0 0 auto;
  width: 43.4782608696px;
}
.sales-content {
  flex: 1;
  font-size: 14.347826087px;
  line-height: 1.8;
}
.sales-group + .sales-group {
  margin-top: 30.4347826087px;
}
.sales-action {
  display: flex;
  justify-content: center;
  gap: 13.0434782609px;
}
.sales-action a {
  flex: 0 0 auto;
  width: 150px;
}
@media only screen and (max-width: 1023px) {
  .sales {
    max-width: 61.5384615385vw;
  }
  .sales-head {
    margin-bottom: 4.6153846154vw;
    font-size: 5.641025641vw;
  }
  .sales-item {
    gap: 2.5641025641vw;
  }
  .sales-item + .sales-item {
    margin-top: 4.2735042735vw;
    padding-top: 4.2735042735vw;
  }
  .sales-pic {
    width: 8.547008547vw;
  }
  .sales-content {
    font-size: 2.8205128205vw;
  }
  .sales-group + .sales-group {
    margin-top: 5.9829059829vw;
  }
  .sales-action {
    gap: 2.5641025641vw;
  }
  .sales-action a {
    width: 29.4871794872vw;
  }
}